.Footer{
    width: 100%;
    height: 150px;
}

.Footer-content{
    width: 100%;
    max-width: 900px;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.Footer-socials{
    display: flex;
    flex-direction: row;
    width: 50%;
    max-width: 250px;
    justify-content: space-between;
    height: fit-content;
}

.Footer-iconContainer{
    width: 40px;
    height: 40px;
}

.Footer-phone{
    height: fit-content;
    font-size: 25px;
    font-weight: 700;
}

.Footer-mail{
    height: fit-content;
    font-size: 25px;
    font-weight: 700;
}

.Footer-icon{
    object-fit: cover;
    width: 100%;
    height: 100%;
}
